import React, { Component } from "react";
import { Container } from "@material-ui/core";

export default class HeaderComponent extends Component {
    render() {
        return (
            <Container style={style.container}  maxWidth="sm" onClick={()=>{window.location.href = "/";}}>
                <img style={style.image} src={link.image_oses} alt="image_oses" />

                <div style={style.heading}>
                    <div>UZUNDERE</div>
                    <div>ERZURUM</div>
                </div>

                <img style={style.image} src={link.image_arzen} alt="image_arzen" />
            </Container>
        );
    }
}

const link = {
    image_oses: "https://www.oses.com.tr/assets/images/oses.png",
    image_arzen: "http://arzen.com.tr/images/arzen_logo.png"
}

const style = {
    container: {
        height: "70px",
        backgroundColor: "whitesmoke",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center"
    },

    image: {
        height: "40px",
        marginTop: "5px"
    },

    heading: {
        display: "block",
        fontWeight: "bold",
        fontSize: "15px",
        marginTop: "5px",
        textAlign: "center"
    },
}