import React, {Component} from "react";
import {Container} from "@material-ui/core";

export default class FooterComponent extends Component {
    render() {
        return(
            <Container style={style.container} maxWidth="sm">
                Muzaffer Müjdeci
            </Container>
        );
    }
}

const style = {
    container : {
        minHeight : "50px",
        backgroundColor: "gray",
        color: "white",
        textAlign: "right",
        paddingTop: "15px"
    }
}