import React, { Component } from "react";
import MobileLayout from "../layout/mobile";

import {
    Button,
    IconButton,
    List,
    ListItem,
    ListSubheader,
    ListItemText
} from "@material-ui/core";

import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import RefreshIcon from '@material-ui/icons/Refresh';
import OrderData from '../data/order';

export default class OrderPage extends Component {

    constructor(props) {
        super(props);

        let queryString = window.location.search;
        let urlParams = new URLSearchParams(queryString);
        let sub_ = urlParams.get('subpage');
        this.state = {
            sub: sub_,
            orders: OrderData[sub_]
        };

        this.changeAmount = this.changeAmount.bind(this);
        this.refreshAmounts = this.refreshAmounts.bind(this);
        this.makeOrder = this.makeOrder.bind(this);
    }

    changeAmount(stickyheader, header, amount) {
        let orders_ = this.state.orders;
        for (let i = 0; i < orders_.length; i++) {
            if (orders_[i].title === stickyheader) {
                for (let j = 0; j < orders_[i].sub.length; j++) {
                    if (orders_[i].sub[j].title === header) {
                        let totalmount = amount * orders_[i].sub[j].step;
                        totalmount += orders_[i].sub[j].amount;
                        if (totalmount < 0) {
                            return;
                        }
                        orders_[i].sub[j].amount = totalmount;
                        this.setState({ orders: orders_ });
                        return;
                    }
                }
            }
        }
    }

    refreshAmounts() {
        let orders_ = this.state.orders;
        for (let i = 0; i < orders_.length; i++) {
            for (let j = 0; j < orders_[i].sub.length; j++) {
                orders_[i].sub[j].amount = 0;
            }
        }
        this.setState({ orders: orders_ });
    }

    makeOrder() {
        let message = "Sipariş vermek istiyorum.%0A%0A";
        let orders_ = this.state.orders;
        for (let i = 0; i < orders_.length; i++) {
            for (let j = 0; j < orders_[i].sub.length; j++) {
                if (orders_[i].sub[j].amount > 0) {
                    let amt = orders_[i].sub[j].amount;
                    let unt = orders_[i].sub[j].unit;
                    let ttl = orders_[i].sub[j].title;
                    let hdr = orders_[i].title;
                    message += `[${amt} ${unt}] ${hdr} > ${ttl}%0A`
                }
            }
        }

        console.log(message);

        window.open(
            'https://wa.me/905364572436?text='+message,
            '_blank'
        );
    }

    render() {
        let card_height = null;
        let fullw = window.innerWidth;
        let fullh = window.innerHeight;
        if (fullw < 600) {
            card_height = (fullh - 245) + "px";
        } else {
            card_height = (fullh - 245) + "px";
        }

        let sticky_headers = [];
        this.state.orders.forEach(e => {
            let headers = [];
            e.sub.forEach(e1 => {
                headers.push(
                    <ListItem key={e1.title}>
                        <div style={style.amountbox}>
                            {(e1.amount !== 0) ? e1.amount + " " + e1.unit : ""}
                        </div>
                        <ListItemText primary={e1.title} />

                        <IconButton style={{ color: "#c8d4aa" }} onClick={(x) => { this.changeAmount(e.title, e1.title, -1) }}>
                            <RemoveCircleOutlineIcon />
                        </IconButton>
                        <IconButton style={{ color: "#a3b52f" }} onClick={(x) => { this.changeAmount(e.title, e1.title, 1) }}>
                            <AddCircleOutlineIcon />
                        </IconButton>
                    </ListItem>
                );
            });
            sticky_headers.push(
                <li key={e.title}>
                    <ul style={{ paddingLeft: "0px" }}>
                        <ListSubheader style={style.stickyheader}>{e.title}</ListSubheader>
                        {headers}
                    </ul>
                </li>
            );
        });

        let content = (
            <div>
                <List subheader={<li />} style={{ overflow: "auto", maxHeight: card_height }}>
                    {sticky_headers}
                </List>
                <div style={{ width: "100%", display: "flex", justifyContent: "space-between" }}>
                    <IconButton style={style.refresh} onClick={this.refreshAmounts}>
                        <RefreshIcon />
                    </IconButton>
                    <Button
                        variant="contained"
                        style={style.orderbutton}
                        endIcon={<ShoppingBasketIcon />}
                        onClick={()=>{
                            this.makeOrder();
                        }}>
                        SİPARİŞ VER
                    </Button>
                </div>
            </div>
        );

        return (
            <MobileLayout
                content={content} />
        );
    }
}

const style = {
    stickyheader: {
        backgroundColor: "#e21e27",
        color: "whitesmoke",
        fontSize: "15px"
    },

    orderbutton: {
        width: "90%",
        marginTop: "5px",
        backgroundColor : "#97d700",
        color: "white"
    },

    refresh: {
        marginTop: "5px"
    },

    amountbox: {
        border: "1.5px solid gray",
        borderRadius: "5px",
        height: "20px",
        width: "50px",
        marginRight: "5px",
        fontSize: "15px",
        textAlign: "right",
        paddingRight: "1px"
    }
}

