import React, { Component } from "react";
import MobileLayout from "../layout/mobile";

import {
    Card,
    CardActionArea,
    CardMedia,
    CardContent,
    Grid,
    IconButton
} from "@material-ui/core";

import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import PhoneIcon from '@material-ui/icons/Phone';
import LocationOnIcon from '@material-ui/icons/LocationOn';

export default class WelcomePage extends Component {
    render() {
        let card_height = null;
        let fullw = window.innerWidth;
        let fullh = window.innerHeight;
        if (fullw < 600) {
            card_height = ((fullh-367)/2) + "px";
        } else {
            card_height = (fullh-305) + "px";
        }

        let cards = [];
        card_info.forEach(e => {
            cards.push(
                <Grid item sm={6} xs={12} key={e.id}>
                    <Card>
                        <CardActionArea onClick={(x)=>{window.location.href=e.to;}}>
                            <CardMedia
                                component="img"
                                alt={e.id}
                                height={card_height}
                                image={e.image} />
                            <CardContent style={style.card_header}>
                                {e.title}
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
            );
        });

        let contacts = [];
        contact_info.forEach(e => {
            contacts.push(
                <IconButton
                    key={e.label}
                    style={{ color: e.color }}
                    aria-label={e.label}
                    onClick={() => {
                        window.open(
                            e.to,
                            '_blank'
                        );
                    }}>
                    {<e.icon fontSize="large" />}
                </IconButton>
            );
        });

        let content = (
            <div>
                <div>
                    <Grid container spacing={1}>
                        {cards}
                    </Grid>
                </div>

                <div>
                    <Grid container spacing={3} style={{justifyContent: "center", marginTop: "20px"}}>
                        {contacts}
                    </Grid>
                </div>

            </div>
        );

        return (
            <MobileLayout
                content={content} />
        );
    }
}

const style = {
    card_header: {
        textAlign: "center",
        fontWeight: "bolder",
        fontSize: "20px"
    }
}

const card_info = [
    {
        id: "oses",
        image: "https://www.oses.com.tr/assets/images/media_promo.jpg",
        title: "OSES SİPARİŞ",
        to: "/?page=order&subpage=oses"
    },
    {
        id: "arzen",
        image: "http://arzen.com.tr/images/menupage/serbetli.jpg",
        title: "ARZEN SİPARİŞ",
        to: "/?page=order&subpage=arzen"
    },
];

const contact_info = [
    {
        icon: WhatsAppIcon,
        to: "https://wa.me/905364572436?text=Sipariş vermek istiyorum.",
        color: "#25d366",
        label: "whatsapp"
    },

    {
        icon: PhoneIcon,
        to: "tel:905364572436",
        color: "#4885ed",
        label: "phone"
    },

    {
        icon: LocationOnIcon,
        to: "https://goo.gl/maps/FTJTrqDY8BF2e6pj8",
        color: "#e94333",
        label: "location"
    }
]